<template>
    <el-row>
        <el-col v-for="(post, key) in posts" :key="key">
            <h1>
                <router-link :to="post.sha | href">{{post.name | title}}</router-link>
            </h1>
        </el-col>
    </el-row>
</template>

<script>
export default {
	name: "Home",
	title: "博客",
	data: function() {
		return {
			posts: [],
			page: 1
		};
	},
	filters: {
		href: function(v) {
			return `/post/${v}`;
		},
		title: function(v) {
			return v.split(".")[0];
		}
	},
	mounted: function() {
		this.$http(
			"https://api.github.com/repos/Ireoo/blog/contents/post?ref=master"
		).then(res => {
			this.posts = res.data;
		});
	}
};
</script>

<style scoped>
h1 > a {
	font-size: 16px;
}
</style>
