	watch: {
		"$route.meta.title": function(v) {
			document.title = `${v} - ${this.$store.state.site.info.page}`;
		}
	},
	mounted: function() {
		document.title = `${this.$route.meta.title} - ${
			this.$store.state.site.info.page
		}`;

		this.$nextTick(function() {});
	}
};
</script>

